// TYPOGRAPHY

body {
	font-family: $defaultfont;
	font-size: 1rem;
	line-height: 1.5;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
	font-family: $mainfont;
	margin: $su / 2 0;
	padding: 0;
	font-weight: 500;
}

h1,
.h1 {
	font-size: 2.250rem;
}

h2,
.h2 {
	font-size: 1.875rem;
}

h3,
.h3 {
	font-size: 1.500rem;
}

h4,
.h4 {
	font-size: 1.125rem;
}

h5,
.h5 {
	font-size: .875rem;
}

h6,
.h6 {
	font-size: .750rem;
}


p {
	margin: $su / 2 0;

	&.lead {
		font-size: 1.5em;
		font-weight: 200;
	}
}
