.pull-right { float: right; }

.pull-left { float: left; }

.hide, .hidden { display: none; }

.show { display: block; }

.invisible { visibility: hidden; }

.text-hide { color: transparent; text-shadow: none; background-color: transparent; border: 0; }

.affix { position: fixed; }

.faded { color: #aaa; }

.video-reponsive { position: relative; padding-bottom: 56.25%; padding-top: 25px; height: 0; }

.video-reponsive iframe { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }
