// UI

.block {
	padding: $su;
	margin: $su 0;
	border: 1px solid $mediumgray;
	@include border-radius;
}

hr {
	margin-top: $su;
	margin-bottom: $su;
	border: 0;
	border-top: 1px solid $superlightgray;
}

ul {margin: $su / 2 0;}

// forms

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
}

.sr-only-focusable {
	&:active,
	&:focus {
		position: static;
		width: auto;
		height: auto;
		margin: 0;
		overflow: visible;
		clip: auto;
	}
}

[role='button'] {
	cursor: pointer;
}

// images

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
	margin: 0 auto;
}

.img-rounded {
	border-radius: 6px;
}

.img-thumbnail {
	padding: 4px;
	line-height: 1.42857143;
	background-color: $white;
	border: 1px solid $lightgray;
	border-radius: 4px;
	transition: all .2s ease-in-out;
	display: inline-block;
	max-width: 100%;
	height: auto;
}

.img-circle {
	border-radius: 50%;
}
