		border-radius: 3px;
	}
}

select.input-group-sm > {
	.form-control,
	.input-group-addon,
	.input-group-btn > .btn {
		height: 30px;
		line-height: 30px;
	}
}

textarea.input-group-sm > {
	.form-control,
	.input-group-addon,
	.input-group-btn > .btn {
		height: auto;
	}
}

select[multiple].input-group-sm > {
	.form-control,
	.input-group-addon,
