.input-group-addon,
.input-group-btn,
.input-group .form-control {
	display: table-cell;
}

.input-group-addon:not(:first-child):not(:last-child),
.input-group-btn:not(:first-child):not(:last-child),
.input-group .form-control:not(:first-child):not(:last-child) {
	border-radius: 0;
}

.input-group-addon,
.input-group-btn {
	width: 1%;
	white-space: nowrap;
	vertical-align: middle;
}

.input-group-addon {
	padding: 6px 12px;
	font-size: 14px;
	font-weight: normal;
	line-height: 1;
	color: $darkgray;
	text-align: center;
	background-color: $lightgray;
	border: 1px solid $mediumgray;
	border-radius: 4px;

	&.input-sm {
		padding: 5px 10px;
		font-size: 12px;
		border-radius: 3px;
	}

	&.input-lg {
		padding: 10px 16px;
		font-size: 18px;
		border-radius: 6px;
	}

	input {
		&[type="radio"],
		&[type="checkbox"] {
			margin-top: 0;
		}
	}
}

.input-group .form-control:first-child,
.input-group-addon:first-child {
	border-bottom-right-radius: 0;
	border-top-right-radius: 0;
}

.input-group-addon:first-child {
	border-right: 0;
}

.input-group .form-control:last-child,
.input-group-addon:last-child {
	border-bottom-left-radius: 0;
	border-top-left-radius: 0;
}

.input-group-addon:last-child {
	border-left: 0;
}

.input-group-btn {
	position: relative;
	font-size: 0;
	white-space: nowrap;

	> .btn {
		position: relative;

		+ .btn {
			margin-left: -1px;
		}

		&:hover,
		&:focus,
		&:active {
			z-index: 2;
		}
	}

	&:first-child > {
		.btn,
		.btn-group {
			margin-right: -1px;
		}

		.btn:not(:first-child),
		.btn-group:not(:first-child) > .btn {
			border-bottom-left-radius: 0;
			border-top-left-radius: 0;
		}

		.btn,
		.btn-group > .btn,
		.dropdown-toggle {
			border-bottom-right-radius: 0;
			border-top-right-radius: 0;
		}
	}

	&:last-child > {
		.btn,
		.btn-group {
			z-index: 2;
			margin-left: -1px;
		}

		.btn:not(:last-child):not(.dropdown-toggle),
		.btn-group:not(:last-child) > .btn {
			border-bottom-right-radius: 0;
			border-top-right-radius: 0;
		}

		.btn,
		.btn-group > .btn,
		.dropdown-toggle {
			border-bottom-left-radius: 0;
			border-top-left-radius: 0;
		}
	}
}
